import * as React from "react";
import { HeadFC, Link, PageProps, graphql } from "gatsby";
import _ from "lodash";

import { Theme } from "../types/common.type";
import { INotFoundContext, INotFoundQueryResult } from "../types/404.type";

import Button from "../components/common/button";
import ContentContainer from "../components/common/contentContainer";

const NotFoundPage = ({
  data: {
    allMarkdownRemark: { nodes },
  },
  pageContext: { markupData },
}: PageProps<INotFoundQueryResult, INotFoundContext>) => {
  const randomPostId = _.sample(nodes)?.id;

  return (
    <div className="min-h-full flex-1 bg-main-100 flex flex-col items-center justify-center text-secondary-100 gap-10 py-10">
      <ContentContainer className="flex-col items-center justify-center gap-10">
        <h1 className="rubik-font text-[72px] md:text-[100px]">404</h1>
        <div className="flex flex-col w-full gap-5 items-center max-w-[544px]">
          <h2 className="text-[24px] md:text-4xl uppercase font-bold text-center">
            {markupData.title}
          </h2>
          <h3 className="text-lg md:text-xl font-light text-center">
            {markupData.subtitle}
          </h3>
          <Link to={`/blog/${randomPostId}`}>
            <Button
              theme={Theme.light}
              aria-label={markupData.btnLabel}
              className="uppercase h-12 px-5 font-bold"
            >
              {markupData.btnLabel}
            </Button>
          </Link>
        </div>
      </ContentContainer>
    </div>
  );
};

export default NotFoundPage;

export const Head: HeadFC = () => <title>404</title>;

export const query = graphql`
  query ($lang: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "posts" }, language: { eq: $lang } }
      }
      sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        id
      }
    }
  }
`;
